export default {
  components: {
    BlogItem: () => import("@/components/BlogItem"),
  },

  data() {
    return {
      blogs: [],
    };
  },
  async mounted() {
    const blog = await this.$axios.get(
      "https://madein-iran.com/blog/wp-json/wl/v1/posts"
    );
    this.blogs = blog.data;
    console.log(this.blogs);
  },
};
